import firebase, { firestore } from '../firebase'

export const createNewShop = (shop) => {
    if (!firebase.auth().currentUser) {
        return new Promise((resolve, reject) => {
            reject('Not logged in!')
        })
    }

    return firestore
        .collection('shops')
        .add({
            name: shop.name,
            description: shop.description,
            address: shop.address,
            queue: 10,
            averageWaitTime: 3,
            uid: firebase.auth().currentUser.uid
        })
}

export const getAllShops = () => {
    return firestore
        .collection('shops')
        .get()
}

export const listenToShop = (setState) => (shopId) => {
    return firestore
        .collection('shops')
        .doc(shopId)
        .onSnapshot(shop => {
            const _shop = { id: shop.id, ...shop.data() }

            setState(prevState => {
                const isExisting = prevState.shops.find(shop => shop.id === _shop.id)

                let updatedShops

                if (isExisting) {
                    updatedShops = prevState.shops.map(shop => {
                        return shop.id === _shop.id
                            ? _shop
                            : shop
                    })
                } else {
                    updatedShops = [...prevState.shops, _shop]
                }

                return {
                    ...prevState,
                    shops: [...updatedShops]
                }
            })
        })
}