import React from 'react'
import './shop-card.styles.css'

import PeopleIcon from '@material-ui/icons/People';
import AccessTimeIcon from '@material-ui/icons/AccessTime';


const ShopCard = ({ shop }) => {

    return (
        <div className="shop-card">
            <h3>{shop.name}</h3>
            <p>{shop.description}</p>
            <p>{shop.address}</p>
            <span className="shop-card__sub"><PeopleIcon /> in queue <span className="shop-card__queue shop-card__green">{shop.queue ? shop.queue : 'unknown'}</span></span>
            <span className="shop-card__sub"><AccessTimeIcon /> waiting time <span className="shop-card__total-wait-time shop-card__blue">{shop.queue * shop.averageWaitTime ? shop.queue * shop.averageWaitTime : 'unknown'}</span></span>
        </div>
    )
}

export default ShopCard