import React, { useState } from 'react'
import './navbar.styles.css'
import { Link } from 'react-router-dom'


const Navbar = () => {
    const [showNavbar, setShowNavBar] = useState(false)

    const navbarToggle = () => {
        setShowNavBar(b => !b)
    }

    return (
        <nav className="navbar">
            <div className="nav-logo-box">
                <Link to="/">WAIT HOW LONG</Link>
            </div>

            <ul className={showNavbar ? null : "navbar-toggle"}>
                <li><Link to="/login">Login</Link></li>
                <li><Link to="/signup">Sign Up</Link></li>
                <li><Link to="/shop/new">New shop</Link></li>
            </ul>

            <div className="navbar-menu" onClick={navbarToggle}>
                <span className={showNavbar ? "navbar-menu__line navbar-menu__cross" : "navbar-menu__line"}></span>
            </div>
        </nav>
    )
}

export default Navbar