import React, { useState } from 'react'
import './signup.styles.css'

import InputText from '../input-text/input-text.component'
import Button from '../button/button.component'
import { handleChange } from '../../utils/handlers'
import { signUpWithEmailAndPassword } from '../../api/users'


const SignUp = ({ history }) => {

    const ERRORS = {
        passwordsNotMatch: 'Passwords do not match!'
    }

    const [state, setState] = useState({
        email: '',
        password: '',
        confirmPassword: '',
        errors: []
    })

    const handleSubmit = (e) => {
        e.preventDefault();

        const { password, confirmPassword } = state

        if (password !== confirmPassword) {
            if (!state.errors.includes(ERRORS.passwordsNotMatch)) {
                setState(p => ({ ...p, errors: [...p.errors, ERRORS.passwordsNotMatch] }))
            }
        } else {
            setState(p => {
                return {
                    ...p,
                    errors: p.errors.filter(error => error !== ERRORS.passwordsNotMatch)
                }
            })

            signUpWithEmailAndPassword({
                email: state.email,
                password: state.password
            })
                .then(_ => {
                    history.push('/')
                })
                .catch(error => console.log('error -->', error))
        }
    }

    return (
        <div>
            <form className="signup-form" onSubmit={handleSubmit}>
                <h3>Sign up</h3>

                {
                    state.errors.length > 0 &&
                    state.errors.map(error => (
                        <div key={error} className="signup-form-error">
                            {error}
                        </div>
                    ))
                }

                <InputText
                    type="email"
                    name="email"
                    label="Email"
                    value={state.email}
                    onChange={handleChange(setState)}
                />

                <InputText
                    type="password"
                    name="password"
                    label="Password"
                    value={state.password}
                    onChange={handleChange(setState)}
                />

                <InputText
                    type="password"
                    name="confirmPassword"
                    label="Confirm Password"
                    value={state.confirmPassword}
                    onChange={handleChange(setState)}
                />

                <Button>Sign Up</Button>
            </form>
        </div>)
}

export default SignUp