import React, { useEffect, useState } from 'react'
import './search-results.styles.css'

import ShopCard from '../shop-card/shop-card.component'

import { listenToShop } from '../../api/shops'


const SearchResults = ({ keyword, results }) => {

    const [state, setState] = useState({
        shops: []
    })

    useEffect(() => {
        console.log(results)
        results.forEach((result) => {
            listenToShop(setState)(result.id)
        })
    }, [results])

    return (
        <div>
            <div className="search-results-hero">
                <h3>
                    {keyword}
                </h3>
            </div>

            {
                results.length
                    ?
                    <div className="search-results-container">
                        {
                            state.shops.map((shop) => (
                                <ShopCard shop={shop} key={shop.id} />
                            ))
                        }
                    </div>
                    :
                    <div>No shops found, try with a different search term.</div>
            }
        </div>
    )
}

export default SearchResults