import React, { useEffect } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

import Homepage from './components/homepage/homepage.component'
import SearchResultsContainer from './containers/search-results/search-results.container'
import NewShop from './components/new-shop/new-shop.component'
import Navbar from './components/navbar/navbar.component'
import Login from './components/login/login.component'
import SignUp from './components/signup/signup.component'

import firebase from './firebase'


const App = () => {
    useEffect(() => {
        firebase.auth().onAuthStateChanged(user => {
            console.log('auth state changed!', user)
            if (!user) {
                console.log('Not logged in')
            } else {
                console.log('Logged in')
            }
        })
    }, [])

    return (
        <Router>
            <Navbar />
            <Switch>
                <Route path="/search/:keyword" component={SearchResultsContainer} />
                <Route path="/shop/new" component={NewShop} />
                <Route path="/login" component={Login} />
                <Route path="/signup" component={SignUp} />
                <Route path="/" component={Homepage} />
            </Switch>
        </Router>
    )
}

export default App