import React, { useEffect, useState } from 'react'
import './search-results.styles.css'

import SearchResults from '../../components/search-results/search-results.component'
import { getAllShops } from '../../api/shops'
import Spinner from '../../components/spinner/spinner.component'

const SearchResultsContainer = ({ match: { params: { keyword } } }) => {
    const [shops, setShops] = useState([])
    const [showSpinner, setShowSpinner] = useState(true)

    useEffect(() => {
        getAllShops()
            .then(docs => {
                const allDocs = []
                docs.forEach((doc) => {
                    const _doc = { id: doc.id, ...doc.data() }
                    allDocs.push(_doc)
                })
                setShops(allDocs)
                setShowSpinner(false)
            })
            .catch(error => console.log('error -->', error))
    }, [])


    const filterShopsByName = () => {
        // getting all shops then filtering by name for now...
        return shops.filter(shop => shop.name.toLowerCase().includes(keyword.toLowerCase()))
    }

    return showSpinner
        ? <Spinner />
        : (
            <SearchResults
                keyword={keyword}
                results={filterShopsByName()} />
        )
}

export default SearchResultsContainer